import React, { useState, useEffect } from 'react';
import mjplogo from './mjplogo.jpeg';
import telanganalogo from './telanganalogo.png';
import ReactGA from 'react-ga';

const Header = () => {
    const collegeName = "Mahatma Jyothiba Phule Telangana Backward Classes Welfare Residential Degree College".toUpperCase();
    const [visitorsCount, setVisitorsCount] = useState(null);

    useEffect(() => {
        ReactGA.pageview(window.location.pathname + window.location.search);
        ReactGA.event({
            category: 'Visitor',
            action: 'Visit'
        });

        ReactGA.getMetrics().then(metrics => {
            setVisitorsCount(metrics.visitors);
        });
    }, []);

    return (
        <div className="bg-zinc-800 w-screen h-40">
            <div className="Header container mx-auto px-4 h-full">
                <div className="flex items-center justify-between py-4 h-full">
                    <div className="flex items-center">
                        <img 
                            src={telanganalogo} 
                            alt="College Logo" 
                            className="mr-6" 
                            style={{ width: '100px', height: '100px', borderRadius: '50%' }} 
                        />
                        <span className="text-white text-xl font-bold" style={{ marginLeft: '20px', marginRight: '20px' }}>{collegeName}</span>
                    </div>

                    <div className="flex flex-col items-center">
                        <img 
                            src={mjplogo} 
                            alt="MJP Logo" 
                            className="mb-2" 
                            style={{ width: '80px', height: '80px' }} 
                        />
                        <span className="text-white text-lg font-semibold mb-2">{visitorsCount ? `Visitors: ${visitorsCount}` : 'Loading...'}</span>
                        <span className="text-white text-lg font-semibold">Learn to Lead</span>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Header;
